<template>
  <div>
    <el-upload
        class="upload-demo"
        :action="objectData.host"
        :before-upload="getPolicy"
        :on-preview="handlePreview"
        :on-remove="handleRemove"
        :on-success="handleAvatarSuccess"
        :file-list="fileList"
        :data="objectData"
        :show-file-list="true">
      <el-button size="small" type="primary">‰∏ä‰º†ÁÖßÁâá</el-button>
      <div slot="tip" class="el-upload__tip">Âè™ËÉΩ‰∏ä‰º†jpg/pngÊñá‰ª∂Ôºå‰∏î‰∏çË∂ÖËøá500kb</div>
      <img :src='imageUrl' v-if="objectData.key">
    </el-upload>
  </div>
</template>

<script>
import {uploaddynamicPicture} from "@/service/userServers";

export default {
  data() {
    return {
      objectData: {
        OSSAccessKeyId: "",
        policy: "",
        Signature: "",
        key: "",
        host: '',
        dir: ''
      },
      fileList: [],
      imageUrl: ''
    };
  },
  methods: {
    getPolicy(file) {
      console.log(file)
      return new Promise(((resolve, reject) => {
        uploaddynamicPicture(file.name)
            .then(res => {
              console.log(res)
              this.objectData.OSSAccessKeyId = res.data.data.accessid; // BucketÊã•ÊúâËÄÖÁöÑAccessKey ID„ÄÇ
              this.objectData.policy = res.data.data.policy; //PolicyËßÑÂÆö‰∫ÜËØ∑Ê±ÇË°®ÂçïÂüüÁöÑÂêàÊ≥ïÊÄß„ÄÇ
              this.objectData.Signature = res.data.data.signature;//Ê†πÊçÆAccessKey SecretÂíåPolicyËÆ°ÁÆóÁöÑÁ≠æÂêç‰ø°ÊÅØÔºåOSSÈ™åËØÅËØ•Á≠æÂêç‰ø°ÊÅØ‰ªéËÄåÈ™åËØÅËØ•PostËØ∑Ê±ÇÁöÑÂêàÊ≥ïÊÄß„ÄÇ
              this.objectData.dir = res.data.data.dir;//ÂâçÁºÄ
              this.objectData.host = res.data.data.host;// "https://" + bucketname + '.' + endpoint;  (ÂâçÁ´ØËØ∑Ê±ÇossÊúçÂä°Ë∑ØÂæÑ)
              this.objectData.key = res.data.data.key;//dir + fileName (‰∏ä‰º†ObjectÁöÑÂêçÁß∞„ÄÇ)
              this.$parent.contents.picture = res.data.data.key
              console.log(this.$parent.contents.picture)
              resolve(true);
            })
            .catch(err => {
              console.log(err);
              reject(false);
            })

      }))
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleAvatarSuccess(response, file){
      this.imageUrl = URL.createObjectURL(file.raw);
    }
  }
}
</script>

<style scoped lang='less'>
.upload-demo{
  img{
    width: 200px;
    height: 200px;
  }
}
.upload-demo{
  margin: 10px 0 0 10px;
}
</style>
